.feature-box {
  /* background: #ededed; */
  box-shadow: inset 22px 22px 44px #d1d1d1, inset -22px -22px 44px #d1d1d1;
  @apply text-xl p-6 text-center rounded-lg transition-all duration-200 shadow-2xl w-11/12 bg-white bg-opacity-50;
  @apply md:w-3/5;
  @apply xl:w-4/5;
  @apply 2xl:w-4/5;
}

.feature-icon {
  @apply text-8xl mb-6 inline-block text-gray-700;
  text-shadow: 0 4px 8px rgb(0 0 0 / 12%), 0 2px 4px rgb(0 0 / 8%);
}

.feature-tertiary {
  @apply text-2xl font-bold;
  @apply text-gray-700;
}

.feature-p {
  @apply text-gray-700 text-base p-2;
}
